<template>
  <v-dialog v-model="isOpen" transition="fade-transition" scrollable>
    <v-card>
      <v-card-title>
        {{ props.item.name }}
        {{ equipment.getNameWithSlots(props.item.slots) }}
      </v-card-title>

      <v-divider></v-divider>
      <v-card-text class="pa-0">
        <template v-for="(info, index) in props.item.infos" :key="index">
          <v-card elevation="0">
            <v-card-text>
              <div class="d-flex flex-row align-start">
                <v-img
                  :src="
                    filePath.getCommentItemImagePath(
                      props.item.imgNames[index],
                      props.item.categoryCode,
                      props.item.isCard
                    )
                  "
                  width="78"
                  max-width="78"
                  class="rounded-sm me-3"
                ></v-img>
                <div>
                  <div v-if="info.name" class="font-weight-bold">
                    {{ info.name }}
                  </div>
                  <template v-for="(line, index) in info.contents" :key="index">
                    <div v-html="line" v-if="line.length > 0"></div>
                    <br v-else />
                  </template>
                  <br />
                  <div>
                    <div>
                      <span v-if="!isSpecialCategory">
                        系列：{{ info.class }}
                      </span>
                      <span
                        class="ms-5"
                        v-if="!isSpecialCategory && !props.item.isCard"
                      >
                        {{
                          equipment.getInfoValueTitle(props.item.categoryCode)
                        }}：{{ info.value }}
                      </span>
                    </div>
                    <div v-if="!isSpecialCategory">
                      <span v-if="info.position.length > 0">
                        位置：{{ info.position }}
                      </span>
                      <span :class="info.position.length > 0 ? 'ms-5' : ''">
                        重量：{{ info.weight }}
                      </span>
                    </div>
                    <div v-if="!isSpecialCategory && !props.item.isCard">
                      <span>要求等級：{{ info.requiresLevel }}</span>
                    </div>
                    <div v-if="!isSpecialCategory && !props.item.isCard">
                      <span>裝備等級：{{ info.equipmentLevel }}</span>
                    </div>
                    <div v-if="!isSpecialCategory && !props.item.isCard">
                      <span>裝備：{{ info.usableBy }}</span>
                    </div>
                  </div>
                </div>
              </div>

              <template
                v-if="
                  !isSpecialCategory &&
                  !props.item.isCard &&
                  info.enchantments.length > 0
                "
              >
                <v-divider class="my-3"></v-divider>
                <div class="text-subtitle-2 font-weight-bold">附魔範例</div>
                <ul class="main-list ms-4 text-secondary">
                  <li
                    v-for="(enchantment, index) in info.enchantments"
                    :key="index"
                  >
                    {{ enchantment.name }}
                    <ul class="sub-list ms-6 text-primary">
                      <li
                        v-for="(detail, indexDetail) in enchantment.detail"
                        :key="indexDetail"
                      >
                        {{ detail }}
                      </li>
                    </ul>
                  </li>
                </ul>
              </template>

              <template
                v-if="
                  props.item.relatedItems && props.item.relatedItems.length > 0
                "
              >
                <v-divider class="my-3"></v-divider>
                <div class="text-subtitle-2 font-weight-bold mb-2">
                  相關套裝
                </div>
                <div
                  class="d-flex flex-row align-center related-item mb-1"
                  v-for="(relatedItem, index) in props.item.relatedItems"
                  :key="index"
                >
                  <v-img
                    :src="
                      filePath.getCommentItemImagePath(
                        relatedItem.imgNames[0],
                        relatedItem.categoryCode,
                        relatedItem.isCard
                      )
                    "
                    width="30"
                    max-width="30"
                    class="rounded-sm me-3"
                  ></v-img>
                  <v-btn
                    variant="text"
                    color="primary"
                    @click="openRelatedItemDialog(relatedItem)"
                  >
                    {{ relatedItem.name }}
                    {{ equipment.getNameWithSlots(relatedItem.slots) }}
                  </v-btn>
                </div>
              </template>
            </v-card-text>
          </v-card>
        </template>
      </v-card-text>

      <v-card-actions class="d-flex justify-end">
        <v-btn
          block
          size="small"
          color="primary"
          @click="emit('setDialogOpenStatus', false)"
        >
          關閉
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>
<script setup>
import { computed } from 'vue';
import { filePath } from '@/lib/filepath';
import { equipment } from '@/lib/equipment';

const props = defineProps(['item', 'isOpen']);

const emit = defineEmits(['setDialogOpenStatus', 'openDialog']);

const isOpen = computed({
  get() {
    return props.isOpen;
  },
  set(value) {
    emit('setDialogOpenStatus', value);
  },
});

const isSpecialCategory = computed(
  () => props.item.categoryCode == 'costume' || props.item.categoryCode == 'pet'
);

function openRelatedItemDialog(item) {
  emit('setDialogOpenStatus', false);
  emit('openDialog', item);
}
</script>
<style></style>
