<template>
  <div class="my-5 mx-3 text-h5 font-weight-medium">
    {{ miscStore.getNewEquipments.title }}
  </div>
  <v-divider></v-divider>
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-item>
      <v-card-title>
        <v-icon aria-hidden="false" size="x-small">mdi-chevron-right</v-icon>
        新裝備
        <span class="ps-1 text-caption text-medium-emphasis">
          New Equipment
        </span>
      </v-card-title>
    </v-card-item>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-text>
          <div
            v-for="(line, index) in miscStore.getNewEquipments.introduction"
            :key="index"
          >
            {{ line }}
          </div>
          <EquipmentCommentTabs
            class="mt-5"
            :equipmentCommentList="miscStore.getNewEquipments.comments"
            :hideEmpty="true"
          />
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { onMounted, onUpdated } from 'vue';
import { useCommonStore } from '@/store/common';
import { useMiscStore } from '@/store/misc';
import EquipmentCommentTabs from '@/components/EquipmentCommentTabs.vue';

const commonStore = useCommonStore();
const miscStore = useMiscStore();

onMounted(async () => {
  await miscStore.fetchNewEquipments();
  await commonStore.updateLoadStatus(true);
});

onUpdated(async () => {
  await commonStore.updateLoadStatus(true);
});
</script>
