<template>
  <v-card
    elevation="0"
    :key="props.item.id"
    class="my-2 py-2 d-flex flex-row rounded"
  >
    <div class="d-flex flex-row align-start">
      <v-img
        v-for="(imgName, index) in props.item.imgNames"
        :key="index"
        :src="
          filePath.getCommentItemImagePath(
            imgName,
            props.item.categoryCode,
            props.item.isCard
          )
        "
        width="78"
        max-width="78"
        :class="!commonStore.isMobileMode ? 'desktop' : getMobileClass(index)"
        class="rounded-sm"
      ></v-img>
    </div>
    <v-card elevation="0" :class="!commonStore.isMobileMode ? 'ms-5' : 'ms-2'">
      <v-card-item class="px-0 pt-0">
        <v-card-title>
          <div class="text-subtitle-2">
            {{ props.item.name }} {{ getSlotText(props.item.slots) }}
          </div>
          <div class="pa-0">
            <template class="d-flex flex-row align-center">
              <v-btn
                class="btn-info me-1"
                color="primary"
                size="x-small"
                variant="outlined"
                @click.stop="openDialog(props.item)"
              >
                <v-icon class="ma-0 icon" aria-hidden="false">
                  mdi-information
                </v-icon>
                INFO
              </v-btn>

              <v-chip
                class="px-1 py-0 me-1"
                size="x-small"
                variant="outlined"
                label
                :color="getAvailableClass(props.item.available)"
              >
                {{ getAvailableText(props.item.available) }}
              </v-chip>

              <div
                :class="!commonStore.isMobileMode ? 'ms-2' : 'ms-0'"
                v-if="props.item.star >= 0"
                class="rank-block"
              >
                <span class="rank me-1 text-body-2 font-weight-medium">
                  {{ props.item.star.toFixed(1) }}
                </span>
                <v-icon
                  icon="mdi-star"
                  v-for="n in Math.floor(props.item.star)"
                  :key="n"
                  color="orange"
                  size="x-small"
                ></v-icon>
                <v-icon
                  icon="mdi-star-half-full"
                  v-if="
                    Math.floor(props.item.star) != Math.ceil(props.item.star)
                  "
                  color="orange"
                  size="x-small"
                ></v-icon>
                <v-icon
                  icon="mdi-star-outline"
                  v-for="n in 5 - Math.ceil(props.item.star)"
                  :key="n"
                  color="orange"
                  size="x-small"
                ></v-icon>
              </div>
            </template>
          </div>
        </v-card-title>
      </v-card-item>

      <v-card-text class="px-0">
        <div v-for="(line, index) in props.item.comments" :key="index">
          {{ line }}
        </div>
        <div v-if="!props.item.isCard" class="text-red mt-5">
          <v-icon size="small">mdi-treasure-chest</v-icon>
          取得方式：{{ props.item.loot }}
        </div>
        <v-card-actions v-if="!props.item.isCard" class="px-0 py-0">
          <v-btn
            v-for="(link, index) in props.item.externalUrls"
            :key="index"
            variant="plain"
            color="secondary"
            class="py-0 px-0"
            :href="link.url"
            target="_blank"
          >
            <v-icon>mdi-open-in-new</v-icon>
            {{ link.name }}
          </v-btn>
        </v-card-actions>
      </v-card-text>
    </v-card>
  </v-card>
</template>
<script setup>
import { useCommonStore } from '@/store/common';
import { filePath } from '@/lib/filepath';

const commonStore = useCommonStore();

const props = defineProps(['item']);
const emit = defineEmits(['openDialog']);

function getAvailableText(available) {
  let text = '';
  if (available) {
    text = '已實裝';
  } else {
    text = '未實裝';
  }
  return text;
}
function getAvailableClass(available) {
  let availableClass = '';
  if (available) {
    availableClass = 'secondary';
  } else {
    availableClass = 'error';
  }
  return availableClass;
}
function getMobileClass(index) {
  if (index == 0) {
    return `mobile-1`;
  } else {
    return `mobile mobile-${index + 1}`;
  }
}
function getSlotText(slots) {
  let text = '';
  if (slots > 0) {
    text = `[${slots}]`;
  }
  return text;
}
function openDialog(item) {
  emit('openDialog', item);
}
</script>

<style scope>
.btn-info {
  height: 20px !important;
}

.v-card-actions {
  min-height: 40px;
}

.rank-block {
  line-height: 1rem;
}

.rank {
  padding-top: 1px;
}

.mobile {
  margin-left: -50px !important;
}

@media (max-width: 530px) {
  .mobile {
    margin-left: -78px !important;
  }
}

.mobile-1 {
  z-index: 6;
}

.mobile-2 {
  z-index: 5;
}

.mobile-3 {
  z-index: 4;
}

.mobile-4 {
  z-index: 3;
}

.mobile-5 {
  z-index: 2;
}

.mobile-6 {
  z-index: 1;
}
</style>
