const equipment = {
  getNameWithSlots(slots) {
    let text = '';
    if (slots > 0) {
      text = `[${slots}]`;
    }
    return text;
  },
  getInfoValueTitle(categoryCode) {
    let title = '';
    switch (categoryCode) {
      case 'weapon':
        title = '攻擊';
        break;

      default:
        title = '防禦';
        break;
    }
    return title;
  },
};

export { equipment };
