import { createRouter, createWebHistory } from 'vue-router';
import HomeView from '@/views/HomeView.vue';
import GuideView from '@/views/GuideView.vue';
import AboutView from '@/views/AboutView.vue';
import NewEquipmentView from '@/views/misc/NewEquipmentView.vue';
import NewElementTableView from '@/views/misc/NewElementTableView.vue';
import EquipmentDatabaseView from '@/views/EquipmentDatabaseView.vue';
import { useCommonStore } from '@/store/common';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/guide/:classType',
    name: 'guide',
    component: GuideView,
    props: true,
  },
  {
    path: '/msic/new_equipment',
    name: 'new_equipment',
    component: NewEquipmentView,
  },
  {
    path: '/msic/new_element_table',
    name: 'new_element_table',
    component: NewElementTableView,
  },
  {
    path: '/contact/about',
    name: 'about',
    component: AboutView,
  },
  {
    path: '/equipment_database',
    name: 'equipment_database',
    component: EquipmentDatabaseView,
  },
  {
    path: '/:catchAll(.*)',
    redirect: '/',
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});
router.beforeEach((to) => {
  const commonStore = useCommonStore();
  commonStore.updateLoadStatus(false);
  commonStore.updateFixedFooterStatus(
    to.name == 'home' || to.name == 'about' || to.name == 'new_equipment'
  );
});
router.afterEach(() => {});
export default router;
