const filePath = {
  getCommentItemImagePath(fileName, categoryCode, isCard) {
    if (categoryCode == 'pet') {
      return filePath.getPetImagePath(fileName);
    } else if (isCard) {
      return filePath.getCardImagePath(fileName);
    } else {
      return filePath.getEquipmentImagePath(fileName);
    }
  },
  getEquipmentImagePath(fileName) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/equipment/${fileName}`;
    }
    return path;
  },
  getCardImagePath(fileName) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/card/${fileName}`;
    }
    return path;
  },
  getPetImagePath(fileName) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/pet/${fileName}`;
    }
    return path;
  },
  getGuideImagePath(fileName, classType) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/guide/${classType}/${fileName}`;
    }
    return path;
  },
  getClassImagePath(fileName) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/class/${fileName}`;
    }
    return path;
  },
  getSkillImagePath(fileName) {
    var path = '';
    if (fileName) {
      path = `${process.env.BASE_URL}image/skill/${fileName}`;
    }
    return path;
  },
};
export { filePath };
