<template>
  <div class="my-5 mx-3 text-h5 font-weight-medium d-flex flex-row">
    <v-img
      :src="filePath.getClassImagePath(guideStore.getGuide.icon)"
      max-height="28"
      max-width="28"
      height="28"
      width="28"
      class="me-1"
    ></v-img>
    {{ guideStore.getGuide.title }}
  </div>
  <v-divider></v-divider>

  <!-- 介紹 Introduction -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title>
      <v-icon aria-hidden="false" size="x-small" class="">
        mdi-chevron-right
      </v-icon>
      {{ guideStore.getGuide.introduction.title }}
      <span class="ps-1 text-caption text-medium-emphasis">
        {{ guideStore.getGuide.introduction.subTitle }}
      </span>
    </v-card-title>
    <v-card-text>
      <div
        v-for="(line, index) in guideStore.getGuide.introduction.content"
        :key="index"
      >
        {{ line }}
      </div>
    </v-card-text>
  </v-card>

  <!-- 配點 Build -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title>
      <v-icon aria-hidden="false" size="x-small">mdi-chevron-right</v-icon>
      {{ guideStore.getGuide.build.title }}
      <span class="ps-1 text-caption text-medium-emphasis">
        {{ guideStore.getGuide.build.subTitle }}
      </span>
    </v-card-title>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-title class="text-subtitle-1">
          <v-icon aria-hidden="false" size="x-small">
            mdi-chevron-double-right
          </v-icon>
          {{ guideStore.getGuide.build.skill.title }}
          <span class="ps-1 text-caption text-medium-emphasis">
            {{ guideStore.getGuide.build.skill.subTitle }}
          </span>
        </v-card-title>
        <v-card-text>
          <div
            v-for="(line, index) in guideStore.getGuide.build.skill.content"
            :key="index"
          >
            {{ line }}
          </div>
          <SkillInfo
            v-for="(info, index) in guideStore.getGuide.build.skill.infos"
            :info="info"
            :key="index"
          ></SkillInfo>
          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in skillImages"
              :url="filePath.getGuideImagePath(image.url, props.classType)"
              :description="image.description"
              :maxWidth="600"
              :key="index"
            />
          </template>
        </v-card-text>
      </v-card>

      <v-card elevation="0" class="mt-2">
        <v-card-title class="text-subtitle-1">
          <v-icon aria-hidden="false" size="x-small">
            mdi-chevron-double-right
          </v-icon>
          {{ guideStore.getGuide.build.state.title }}
          <span class="ps-1 text-caption text-medium-emphasis">
            {{ guideStore.getGuide.build.state.subTitle }}
          </span>
        </v-card-title>
        <v-card-text>
          <div
            v-for="(line, index) in guideStore.getGuide.build.state.content"
            :key="index"
          >
            {{ line }}
          </div>

          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in stateImages"
              :url="filePath.getGuideImagePath(image.url, props.classType)"
              :description="
                image.description + guideStore.getGuide.build.state.remark
              "
              :maxWidth="400"
              :key="index"
            />
          </template>
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>

  <!-- 裝備 Equipment -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title>
      <v-icon aria-hidden="false" size="x-small">mdi-chevron-right</v-icon>
      {{ guideStore.getGuide.equipment.title }}
      <span class="ps-1 text-caption text-medium-emphasis">
        {{ guideStore.getGuide.equipment.subTitle }}
      </span>
    </v-card-title>
    <v-card-text class="px-0">
      <v-card elevation="0">
        <v-card-text>
          <div
            v-for="(line, index) in guideStore.getGuide.equipment.content"
            :key="index"
          >
            {{ line }}
          </div>
          <template class="d-flex flex-wrap">
            <PictureWithText
              v-for="(image, index) in equipmentImages"
              :url="filePath.getGuideImagePath(image.url, props.classType)"
              :description="image.description"
              :maxWidth="400"
              :key="index"
            />
          </template>
          <EquipmentCommentTabs
            :equipmentCommentList="guideStore.getGuide.equipment.comments"
          />
        </v-card-text>
      </v-card>
    </v-card-text>
  </v-card>

  <!-- 影片 Video -->
  <v-card elevation="0" class="mx-3 my-2 paragraph">
    <v-card-title>
      <v-icon aria-hidden="false">mdi-chevron-right</v-icon>
      {{ guideStore.getGuide.video.title }}
      <span class="ps-1 text-caption text-medium-emphasis">
        {{ guideStore.getGuide.video.subTitle }}
      </span>
    </v-card-title>
    <v-card-text>
      <div
        v-for="(line, index) in guideStore.getGuide.video.content"
        :key="index"
      >
        {{ line }}
      </div>
      <div
        v-for="(url, index) in guideStore.getGuide.video.urls"
        :key="index"
        class="video-block ratio ratio-16x9"
      >
        <iframe
          width="560"
          height="315"
          :src="url"
          frameborder="0"
          allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
          allowfullscreen
        ></iframe>
      </div>
    </v-card-text>
  </v-card>
</template>

<script setup>
import { ref, watch, onMounted } from 'vue';
import EquipmentCommentTabs from '@/components/EquipmentCommentTabs.vue';
import SkillInfo from '@/components/SkillInfo.vue';
import PictureWithText from '@/components/PictureWithText.vue';
import { useGuideStore } from '@/store/guide';
import { useCommonStore } from '@/store/common';
import { logger } from '@/lib/logger';
import { filePath } from '@/lib/filepath';

const commonStore = useCommonStore();
const guideStore = useGuideStore();
const skillImages = ref([
  {
    url: `skill_1.png`,
    description: '1轉技能配點',
  },
  {
    url: `skill_2.png`,
    description: '2轉技能配點',
  },
  {
    url: `skill_3.png`,
    description: '3轉技能配點',
  },
  {
    url: `skill_4.png`,
    description: '4轉技能配點',
  },
]);
const stateImages = ref([
  {
    url: `state.png`,
    description: '素質配點',
  },
]);
const equipmentImages = ref([
  {
    url: `equipment_1.png`,
    description: '一般裝備',
  },
  {
    url: `equipment_2.png`,
    description: '服飾、影子裝備',
  },
]);

const props = defineProps(['classType']);

onMounted(async () => {
  logger.info(`onMounted:${props.classType}`);
  await loadGuideContent();
  await commonStore.updateLoadStatus(true);
});

watch(
  () => props.classType,
  async () => {
    logger.info(`watch:${props.classType}`);
    await loadGuideContent();
    await commonStore.updateLoadStatus(true);
  }
);

async function loadGuideContent() {
  await guideStore.fetchGuide(props.classType);
  backToTop();
}
function backToTop() {
  window.scrollTo({
    top: 0,
    behavior: 'smooth',
  });
}
</script>
<style scoped></style>
