import { defineStore } from 'pinia';
import axios from 'axios';

export const useEquipmentStore = defineStore('equipment', {
  state: () => {
    return {
      equipment: {},
      equipments: [],
    };
  },
  getters: {
    getEquipment: (state) => {
      return state.equipment;
    },
    getEquipments: (state) => {
      return state.equipments;
    },
  },
  actions: {
    async fetchEquipment(id, categoryCode, isCard) {
      const responseEquipmentDb = (
        await axios.get(`${process.env.BASE_URL}database/equipments.json`)
      ).data;

      this.equipment = findItem(responseEquipmentDb, categoryCode, isCard, id);
    },
    async fetchEquipmentsByName(name) {
      const responseEquipmentDb = (
        await axios.get(`${process.env.BASE_URL}database/equipments.json`)
      ).data;

      this.equipments = findItemsByName(responseEquipmentDb, name);
    },
  },
});

function findItem(responseEquipmentDb, categoryCode, isCard, id) {
  const category = responseEquipmentDb.equipments.find((c) => {
    return c.categoryCode == categoryCode;
  });
  let dbItem;
  if (!isCard) {
    dbItem = category.items.find((item) => {
      return item.id == id;
    });
  } else {
    dbItem = category.cards.find((card) => {
      return card.id == id;
    });
  }
  dbItem.categoryCode = category.categoryCode;
  dbItem.isCard = isCard;
  dbItem.relatedItems = findItems(responseEquipmentDb, dbItem.relatedIds);
  return dbItem;
}

function findItems(responseEquipmentDb, ids) {
  let finalItems = [];
  responseEquipmentDb.equipments.forEach((category) => {
    if (!ids || ids.length == 0) {
      return;
    }
    let items = category.items
      .filter((item) => {
        return ids.includes(item.id);
      })
      .map((item) => {
        return {
          id: item.id,
          name: item.name,
          slots: item.slots,
          categoryCode: category.categoryCode,
          isCard: false,
          imgNames: item.imgNames,
        };
      });
    finalItems = finalItems.concat(items);
    ids = ids.filter((id) => {
      return !items.map((item) => item.id).includes(id);
    });
    let cards = category.cards
      .filter((card) => {
        return ids.includes(card.id);
      })
      .map((card) => {
        return {
          id: card.id,
          name: card.name,
          categoryCode: category.categoryCode,
          isCard: true,
          imgNames: card.imgNames,
        };
      });
    finalItems = finalItems.concat(cards);
    ids = ids.filter((id) => {
      return !cards.map((card) => card.id).includes(id);
    });
  });
  return finalItems;
}

function findItemsByName(responseEquipmentDb, name) {
  let finalItems = [];
  responseEquipmentDb.equipments.forEach((category) => {
    let items = category.items.filter((item) => {
      return item.name.indexOf(name) >= 0;
    });
    finalItems = finalItems.concat(items);

    let cards = category.cards.filter((card) => {
      return card.name.indexOf(name) >= 0;
    });
    finalItems = finalItems.concat(cards);
  });
  return finalItems;
}
