<template>
  <div>
    <v-text-field
      label="裝備名稱"
      v-model="name"
      @update:modelValue="findItem"
    ></v-text-field>
    <pre>{{ equipmentStore.getEquipments }}</pre>
  </div>
</template>
<script setup>
import { ref, onMounted } from 'vue';
import { useEquipmentStore } from '@/store/equipment';
import { useCommonStore } from '@/store/common';

const name = ref('');

const commonStore = useCommonStore();
const equipmentStore = useEquipmentStore();

onMounted(async () => {
  await commonStore.updateLoadStatus(true);
});

async function findItem() {
  if (name.value) {
    await equipmentStore.fetchEquipmentsByName(name.value);
  }
}
</script>
