import { defineStore } from 'pinia';
import axios from 'axios';
import { logger } from '@/lib/logger';

export const useCommonStore = defineStore('common', {
  state: () => {
    return {
      packageVersion: process.env.PACKAGE_VERSION || '0',
      isLoaded: false,
      pages: [],
      fixedFooter: false,
      shiftBackToTop: false,
      windowSize: {
        x: 0,
        y: 0,
      },
      discordLoaded: false,
    };
  },
  getters: {
    appVersion: (state) => {
      return state.packageVersion;
    },
    isMobileMode: (state) => {
      return state.windowSize.x < 1280;
    },
    isDiscordLoad: (state) => {
      return state.discordLoaded;
    },
  },
  actions: {
    updateLoadStatus(isloaded) {
      this.isLoaded = isloaded;
      logger.info(`isloaded:${isloaded}`);
    },
    async fetchPages() {
      let pages = (
        await axios.get(`${process.env.BASE_URL}database/pageLink.json`)
      ).data;
      pages.forEach((page) => {
        page.subPages = page.subPages.filter((p) => p.enable == true);
      });
      this.pages = pages;
    },
    updateFixedFooterStatus(enable) {
      this.fixedFooter = enable;
    },
    updateShiftBackToTop(needShift) {
      this.shiftBackToTop = needShift;
    },
    updateWindowSize(windowSize) {
      this.windowSize = windowSize;
    },
    updateDiscordLoadStatus(loaded) {
      this.discordLoaded = loaded;
    },
  },
});
