const logger = {
    info(message) {
        if (process.env.NODE_ENV.trim() === 'development') {
            console.info(message)
        }
    },
    warn(message) {
        if (process.env.NODE_ENV.trim() === 'development') {
            console.warn(message)
        }
    },
    error(message) {
        if (process.env.NODE_ENV.trim() === 'development') {
            console.error(message)
        }
    },
    trace(message) {
        if (process.env.NODE_ENV.trim() === 'development') {
            console.trace(message)
        }
    }
}

export { logger };